.liveButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: rgb(92, 92, 92);
  background: rgb(255, 255, 255);
  font-family: Arial;
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20% 0 0 20%;
  float: left;
  cursor: pointer;
  transition-duration: 0.2s; 
  border: none;
} 

.liveButton img {
  margin: 5px 5px 5px 2px;
  float: left;
  width: 20px;
}

.liveButton:hover {
  background-color: #ebe5e5;
}

.plannedButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: rgb(92, 92, 92);
  background: rgb(255, 255, 255);
  font-family: Arial;
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0 0% 0% 0;
  float: left;
  cursor: pointer;
  transition-duration: 0.2s; 
  border: none;
} 

.plannedButton img {
  margin: 5px 5px 5px 2px;
  float: left;
  width: 20px;
}

.plannedButton:hover {
  background-color: #ebe5e5;
}

.showAllButton {
  background: rgb(255, 255, 255);
  font-size: 18px;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 0 20% 20% 0;
  cursor: pointer;
  transition-duration: 0.2s; 
  border: none;
} 

.showAllButton img {
  margin: 2px 2px 2px 2px;
  width: 30px;
}

.showAllButton:hover {
  background-color: #ebe5e5;
}

.searchBox {
  position: absolute;
  padding: 0px 40px 0px 0px;
  top: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 11;
  background: white;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 10px;
  border: none;
}

.searchBox input {
  padding: 10px 20px;
  font-size: 20px;
  border-radius: 10px;
  border: none;
}

.searchImgButton {
  position: absolute;
  top: 0.45rem;
  left: 18rem;
  border: none;
  cursor: pointer;
  background: none;
}

.findLocationButton {
  position: absolute;
  top: 8.23rem;
  left: 15.8rem;
  z-index: 10;
  background: white;
  box-shadow: rgb(0 0 0 / 40%) 0px 1px 4px -1px;
  border-radius: 20%;
  border:  none;
  cursor: pointer;
  transition-duration: 0.2s; 
}

.findLocationButton img {
  margin: 2px 0px 2px 0px;
  float: left;
}

.findLocationButton:hover {
  background-color: #ebe5e5;
}

.report-container {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.reportPowerOutage {
  color: #fff;
  background-color: #7ac29a;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s; 
}

.reportPowerOutage:hover {
  background-color: #16bd61;
}

.reportNetworkHazard {
  color: #fff;
  background-color: #6d6b68;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s; 
}

.reportNetworkHazard:hover {
  background-color: #383736;
}

.cancelReport {
  color: #fff;
  background-color: #e97c50;
  font-size: 15px;
  border-radius: 50px;
  padding: 5px;
  border: none;
  cursor: pointer;
  transition-duration: 0.2s; 
}

.cancelReport:hover {
  background-color: #d44b14;
}

.info-window{
  display: flex;
  gap: 5px;
  flex-direction: column;
  font-family: Arial;
}

.img-txt{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  font-family: Arial,sans-serif;
  font-weight: 400;
  font-size: 15px;
  max-width: 25rem;
}

.filter-container {
  display: flex;
  position: absolute;
  top: 8rem;
  left: 1.6rem;
  flex-direction: row;
}